body{
 
  font-family: 'Raleway', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.content{
  padding: 10px 40px 10px 40px;
  line-height: 1.5;
  letter-spacing: -0.004em;
  font-size: 18px;
  color: rgba(0,0,0,0.8);
  margin-bottom: 100px;
  overflow: auto;
}


.banner{
 
}

.bp3-dark a, .bp3-dark a:hover {
  color: white;
}

.footer{
  width: 100%;
  height: 80px;
  position: fixed;
  bottom:0;
  background-color: #394b59;
  opacity: 0.95;
}
.buttonbar{
  display:flex;
  flex: 1 1 0;
  flex-direction:row;
  justify-content:center;
  align-items:center;
}

.button {
  background-color: #F75897;
  color: white;
  margin:20px;
  padding: 10px;
  text-align: center;
  border-radius: 5px;
  border: 2px solid white;
}
 a:hover{
   text-decoration: none;
 }

 a{
   text-decoration: none;
 }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
